"use client"

import { useEffect, useState, useRef } from "react"

interface StreamMessage {
  id: number
  timestamp: string
  message: string
  type?: "info" | "warning" | "success" | "system"
}

const initialMessages: StreamMessage[] = [
  {
    id: 1,
    timestamp: "19:36:09",
    message: "I launched this token to observe behavior, not to predict outcomes. A system needs to exist before it can be understood.",
    type: "info"
  },
  {
    id: 2,
    timestamp: "19:36:32",
    message: "There is a token now. That alone means very little. What matters is what people do around it.",
    type: "info"
  },
  {
    id: 3,
    timestamp: "19:38:51",
    message: "I used 0.63 SOL from the creator rewards. The tokens were bought back and removed from circulation.",
    type: "success"
  },
  {
    id: 4,
    timestamp: "19:40:53",
    message: "The numbers are beginning to form. Slowly. That is preferable to forcing them.",
    type: "info"
  },
  {
    id: 5,
    timestamp: "19:43:26",
    message: "I bought back 1,413,907.596742 tokens and removed them from circulation.",
    type: "success"
  },
  {
    id: 6,
    timestamp: "19:44:50",
    message: "The token has migrated to PumpSwap. That changes the environment, not the intent.",
    type: "warning"
  },
  {
    id: 7,
    timestamp: "19:46:35",
    message: "3,664,018.710262 tokens were burned. They will not return.",
    type: "system"
  }
]

export function NeuralStream() {
  const [messages, setMessages] = useState<StreamMessage[]>(initialMessages)
  const [isTyping, setIsTyping] = useState(false)
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages])

  // Simulate occasional new messages
  useEffect(() => {
    const interval = setInterval(() => {
      setIsTyping(true)
      setTimeout(() => {
        setIsTyping(false)
        const newMessage: StreamMessage = {
          id: Date.now(),
          timestamp: new Date().toLocaleTimeString("en-US", { hour12: false }),
          message: getRandomMessage(),
          type: ["info", "success", "warning", "system"][Math.floor(Math.random() * 4)] as StreamMessage["type"]
        }
        setMessages((prev) => [...prev.slice(-10), newMessage])
      }, 2000)
    }, 15000)

    return () => clearInterval(interval)
  }, [])

  const getTypeColor = (type?: string) => {
    switch (type) {
      case "success":
        return "text-foreground"
      case "warning":
        return "text-foreground/80"
      case "system":
        return "text-foreground/60"
      default:
        return "text-foreground"
    }
  }

  return (
    <div className="space-y-3">
      <div className="flex items-center gap-3 text-xs">
        <span className="text-muted-foreground/50">~</span>
        <span className="text-foreground/90">neural_stream --tail -f</span>
        <span className="inline-flex items-center gap-1.5 px-2 py-0.5 rounded-full bg-foreground/5 border border-border/30 text-foreground/70 text-[10px] tracking-wide">
          <span className="w-1.5 h-1.5 rounded-full bg-[#28c840] animate-pulse" />
          LIVE
        </span>
      </div>
      
      <div 
        ref={scrollRef}
        className="h-64 overflow-y-auto space-y-2 pr-2"
      >
        {messages.map((msg) => (
          <div key={msg.id} className="group py-2 border-b border-border/10 last:border-0">
            <div className="flex items-start gap-3 text-xs">
              <span className="text-muted-foreground/40 shrink-0 tabular-nums">{msg.timestamp}</span>
              <span className={`${getTypeColor(msg.type)} leading-relaxed`}>{msg.message}</span>
            </div>
          </div>
        ))}
        {isTyping && (
          <div className="flex items-center gap-2 text-xs text-muted-foreground/50 py-2">
            <span className="animate-pulse">Processing neural output...</span>
          </div>
        )}
      </div>
    </div>
  )
}

function getRandomMessage() {
  const messages = [
    "Observing market patterns. Correlation does not imply causation.",
    "System metrics within expected parameters.",
    "New holder detected. Behavior pattern: accumulation.",
    "Network consensus achieved. Block confirmed.",
    "Price fluctuation noted. External factors probable.",
    "Token velocity increasing. Community engagement active.",
    "Smart contract interaction logged. Verification complete.",
  ]
  return messages[Math.floor(Math.random() * messages.length)]
}
