export function ActivityLog() {
  const getTypeStyle = (type: ActivityItem["type"]) => {
    switch (type) {
      case "burn":
        return { color: "text-foreground/60", prefix: "BURN" }
      case "claim":
        return { color: "text-foreground/80", prefix: "CLAIM" }
      case "airdrop":
        return { color: "text-foreground/70", prefix: "DROP" }
      case "buyback":
        return { color: "text-foreground", prefix: "BUY" }
      case "launch":
        return { color: "text-foreground/90", prefix: "INIT" }
      default:
        return { color: "text-foreground", prefix: "LOG" }
    }
  }

  return (
    <div className="space-y-3">
      <div className="flex items-center gap-3 text-xs">
        <span className="text-muted-foreground/50">~</span>
        <span className="text-foreground/90">cat /var/log/activity.log</span>
      </div>
      
      <div className="h-48 overflow-y-auto space-y-0 font-mono text-xs">
        {activities.map((activity) => {
          const style = getTypeStyle(activity.type)
          return (
            <div key={activity.id} className="flex items-center gap-3 py-2.5 border-b border-border/10 last:border-0 hover:bg-foreground/[0.02] transition-colors">
              <span className="text-muted-foreground/40 w-16 tabular-nums">{activity.timestamp}</span>
              <span className={`${style.color} w-14 uppercase text-[10px] tracking-wider`}>{style.prefix}</span>
              <span className="text-foreground/70 flex-1">{activity.description}</span>
              {activity.value && (
                <span className="text-foreground/90 tabular-nums">{activity.value}</span>
              )}
            </div>
          )
        })}
      </div>
    </div>
  )
}
